.calculator {
  display: grid;
  grid-gap: 1rem;
  background: $light-gray;
  padding: 1rem;
  @include bp(med) {
    grid-template-columns: 3fr 1fr 3fr;
    justify-content: center;
    align-items: center;

    padding: 3.6rem;
    border-radius: 4px;
  }
  &__currency-A {
    margin-bottom: 1em;
    @include bp(med) {
      margin-bottom: 0;
    }
  }
  &__label-A {
    font-size: 1.2em;
  }
  &__select-label {
    padding-right: 0.5rem;
  }
  &__input--style {
    padding: 0.5rem 1rem;
    width: 100%;
  }
  &__input-A {
    margin-top: 0.2rem;
  }
  &__input-B {
    margin-top: 0.5rem;
  }
  &__equals-sign {
    display: none;
    @include bp(med) {
      display: flex;
      justify-content: center;
      align-self: flex-end;
      font-size: 2.2em;
    }
  }
}
